  --color-invert-o-95: rgba(255 255 255 / 95%);
  --color-vk: rgb(50 100 174);
  --color-vk-dark: rgb(36, 91, 169);
  --color-vk-o-25: rgba(50 100 174 / 25%);
  --color-vk-o-10: rgba(50 100 174 / 10%);
