  --z-index-header: 5;
  --z-index-darker: 7;
  --z-index-panel: 9;
  --header-back-blur: blur(48px);
  --header-back-shadow: inset 0 -1px 0 var(--color-violet-medium-o-25);
  --footer-shadow: inset 0 1px 0 var(--color-violet-medium-o-25);
  --logo-shadow: inset 0 2px 4px rgba(0,0,0,0.250);
  --tile-shadow: 0 4px 8px 8px rgba(0,0,0,0.040);
  --tile-shadow-hover: 0 100px 80px rgba(89 0 170 / 0.070), 0 64.81px 46.85px rgba(89 0 170 / 0.053), 0 38.52px 25.48px rgba(89 0 170 / 0.043), 0 20px 13px rgba(89 0 170 / 0.035), 0 8.15px 6.52px rgba(89 0 170 / 0.027), 0 1.85px 3.15px rgba(89 0 170 / 0.017);
