}
.checkbox__fake-input {
  width: 24px;
  height: 24px;
  padding: 6px;
  background: var(--color-white);
  border: 1px solid var(--color-primary-o-25);
  border-radius: 2px;
  cursor: pointer;
  transition: background 0.125s ease;
}
.checkbox__fake-input::after {
  display: block;
  width: 10px;
  height: 10px;
  border: 3px solid transparent;
  transition: border-color 0.125s ease;
  content: "";
}
.checkbox__input:checked + .checkbox__fake-input {
  background: var(--color-white);
}
.checkbox__input[disabled] + .checkbox__fake-input {
  border: 1px solid var(--color-primary-o-10);
  cursor: not-allowed;
}
.checkbox__input[disabled]:checked + .checkbox__fake-input {
  background: var(--color-primary-o-10);
  border: 1px solid var(--color-primary-o-10);
  cursor: not-allowed;
}
.checkbox__input:checked + .checkbox__fake-input::after {
  border-color: var(--color-violet-medium);
}
.checkbox__input[disabled]:checked + .checkbox__fake-input::after {
  border-color: var(--color-violet-medium);
  opacity: 0.5;
}
.checkbox__label {
  font-size: 12px;
  line-height: 1.25;
  cursor: pointer;
  user-select: none;
}
